[class*='-gradien'] {
  &:hover {
    background-size: 50% 100%;
    transition: all 0.3s ease;
    color: $white;
  }
}
.btn-square {
  @include btn-squre(0);
}
.btn-pill {
  @include btn-squre(60);
}
.btn-group-pill {
  .btn {
    &:first-child {
      @include btn-pill-first(60);
    }
    &:last-child {
      @include btn-pill-last(60);
    }
  }
}
.btn-group-square {
  .btn {
    &:first-child {
      @include btn-pill-first(0);
    }
    &:last-child {
      @include btn-pill-last(0);
    }
  }
}
.btn-showcase {
  .btn {
    margin-bottom: 10px;
    margin-right: 18px;
  }
  margin-bottom: -10px;
}
.btn-air-primary.btn-light.active {
  color: $theme-body-font-color !important;
}
.btn-outline-light.disabled {
  color: $theme-body-font-color;
}
.btn-dropdown-showcase {
  .dropdown {
    margin-bottom: 10px;
    margin-right: 18px;
  }
  .btn-group {
    margin-bottom: 10px;
    margin-right: 18px;
  }
  margin-bottom: -10px;
}
.btn-group-showcase {
  .btn-radio,
  .btn-option {
    label {
      color: $white;
    }
    .txt-dark {
      label {
        color: $theme-body-font-color;
      }
    }
  }
}
.btn-group {
  .radio {
    label {
      &::before {
        top: 2px;
      }
      &::after {
        top: 7px;
      }
    }
  }
  .checkbox {
    label {
      margin-top: 3px;
    }
  }
}
.btn-group-showcase {
  > div {
    margin-bottom: -20px;
    label {
      margin-bottom: 0;
    }
  }
  .btn-group {
    margin-right: 20px;
    margin-bottom: 20px;
  }
  .checkbox {
    input[type='checkbox'] {
      display: none;
    }
  }
}
.tooltiptext {
  visibility: visible;
  width: 120px;
  background-color: #555;
  color: $white;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -60px;
  opacity: 1;
  transition: opacity 0.3s;
  &::after {
    content: '';
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
  }
}
.topright-general {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  display: inline-grid;
  align-items: center;
  justify-content: center;
  &:before {
    transform: rotate(45deg);
  }
}
.toprightarrow-primary {
  color: $primary-color;
  background-color: rgba($primary-color, 0.1);
  @extend .topright-general;
}
.toprightarrow-secondary {
  color: $secondary-color;
  background-color: rgba($secondary-color, 0.1);
  @extend .topright-general;
}
.btn-arrow {
  font-size: 12px;
  font-weight: 700;
  display: flex;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  width: fit-content;
  margin-left: auto;
  margin-right: auto;
  &.arrow-primary {
    color: $primary-color;
  }
  &.arrow-secondary {
    color: $secondary-color;
  }
}
.btn-iconsolid {
  border-radius: 5px;
  background-color: $primary-color;
  padding: 8px 12px;
  i {
    color: $white;
    font-weight: 700;
  }
}
/**=====================
  11. Button CSS end
==========================**/
