#list {
  min-height: 288px;
}
.e-bigger #list,
.e-bigger #tree1,
.e-bigger #tree2 {
  height: 354px;
  overflow: auto;
}
.tree1-data,
.tree2-data,
.tree3-data {
  padding: 15px;
  margin-bottom: 25px;
  width: 50%;
}
#tree1,
#tree2,
#list {
  height: 300px;
  overflow: auto;
}
.content {
  margin: 0 auto;
  border: 1px solid #dddddd;
  border-radius: 3px;
  min-height: 288px;
}
.custom-delete::before {
  content: "\e700";
  cursor: pointer;
  color: rgba(0, 0, 0, 0.54);
  font-size: 15px;
}
.custom-delete {
  float: right;
  font-family: "cross-circle";
}
.e-rtl .custom-delete {
  float: left;
}
.fabric.e-bigger #list.e-listview .e-list-item,
.highcontrast.e-bigger #list.e-listview .e-list-item {
  line-height: 43px;
}
.material.e-bigger #list.e-listview .e-list-item {
  line-height: 48px;
}
.bootstrap.e-bigger #list.e-listview .e-list-item {
  line-height: 47px;
}
.highcontrast .custom-delete::before {
  color: #fff;
}
.highcontrast .e-active.e-list-item .custom-delete::before {
  color: #000;
}
.bootstrap4.e-bigger #list,
.bootstrap4.e-bigger #tree1,
.bootstrap4.e-bigger #tree2 {
  height: 384px;
  overflow: auto;
}
.bootstrap4 #list,
.bootstrap4 #tree1,
.bootstrap4 #tree2 {
  height: 320px;
  overflow: auto;
}
.bootstrap4.e-bigger .e-drag-item.e-treeview .e-list-text {
  margin-left: 12px;
}
#overlay {
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  z-index: 2;
  cursor: pointer;
  background-image: url("https://ej2.syncfusion.com/react/demos/src/images/useCase/drag-and-drop.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: 70%;
}
#imageContent {
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
/* .custom-tree {
        overflow: auto;
    } */
.custom-tree .control-wrapper {
  position: relative;
  min-width: 700px;
  min-height: 400px;
  overflow: auto;
}
@media (max-width: 1200px) {
  .tree1-data,
  .tree2-data,
  .tree3-data {
    width: 33.33333333%;
    float: left;
  }
}
@font-face {
  font-family: "cross-circle";
  src: url(data:application/x-font-ttf;charset=utf-8;base64,)
    format("truetype");
  font-weight: normal;
  font-style: normal;
}
